.type-detail{
    .tooltip{
        display: none !important;
    }
}
.p-detail{
    display: flex;
    flex-direction: column;
}
.p-detail-inner{
    padding: 40px 0 60px 0;
    height: 100%;
}
.p-detail-inner > .p-detail-inner-header{
    display: none;
}
.browse-p{
    order: 10;
}
.p-detail-tabs-wrapper{
    order: 20;
}

.p-detail-inner-header{
    order: 0;
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 10px;

    h1{
        width: 100%;
        order: 0;
        font-weight: 600;
        line-height: 1.2;
        margin-bottom: 20px;
        font-size: 30px;
    }
    .p-code{
        display: flex;
        order: 2;
        font-size: 13px;
        padding: 0 20px 0 0;

        .p-code-label{
            padding-right: 2px;
        }
    }
    div:nth-child(3){
        order: 3;
        font-size: 13px;
    }
    .stars-wrapper{
        margin: 0;
        order: 1;
    }
    .flags{
        display: none;
    }
}
@media screen and (max-width: 500px){
    .p-detail-inner-header .stars-wrapper{
        width: 100%;
    }
    .p-detail-inner-header .p-code {
        padding: 10px 10px 0 0;
    }
    .p-detail-inner-header div:nth-child(3) {
        padding-top: 10px;
    }
}

#product-detail-form{

}
.product-top{
    position: relative;
}
.p-detail-info{
    z-index: 1;
    position: absolute;
}
.p-detail-info :not(.flags){
    display: none;
}
.p-detail-info .flags{
    position: absolute;
    top: 10px;
    left: 10px;
    display: flex;
    flex-direction: column;
}
.p-detail-info .flag{
    margin-bottom: 5px;
    padding: 0 5px;
    height: 22px;
    background: $white;
    white-space: nowrap;
    color: $gray-800;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 13px;

    &.flag-action{
        background: #cf4823;
        color: $white;
    }
    &.flag-new{
        background: #d86329;
        color: $white;
    }
    &.flag-tip{
        background: #eb8959;
        color: $white;
    }
    &.flag-gift{
        background: #a0cd54;
        color: $white;
    }
    &.flag-custom3{
        background: #78aa66;
        color: $white;
    }
}

.p-detail-info .flag .price-standard{
    display: none;
}
.p-image .flag-discount .price-save{
    position: absolute;
    right: 40px;
    top: 10px;  
    padding: 10px;  
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: $white;
    background: $orange-700;
}
.p-image .flag-discount .price-save span{
    padding: 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: $white;
    background: $orange-700;
}
.p-image .flag-discount .price-save span.default-variant{
    display: flex;
}
.p-image .flag-discount .price-save span.default-variant.no-display{
    display: none;
}
.p-image .flag-discount .price-standard{
    display: none !important;
}

.stars-wrapper{
    order: 4;
    margin-bottom: 10px;
    display: flex;
}
.stars-wrapper .stars-label{
    padding-left: 5px;
    font-size: 13px;
    color: $gray-700;
    font-weight: 400;
    align-items: center;
    display: flex;
}
.p-image-wrapper{
    padding-right: 30px;
    width: 60%;
}
.p-image{
    border-radius: $border-radius-primary;
    overflow: hidden;
}
.p-image >div{

}
.p-image a{
    background: $gray-100;
}
.p-image a img{
    mix-blend-mode: multiply;
}
.p-image .mousetrap{
    display: none;
}
.p-thumbnails-wrapper{
    margin-top: 15px;
}
.p-thumbnails{
    position: relative;
}
.p-thumbnails-inner{
    overflow: hidden;
}
.p-thumbnails-inner > div{
    display: flex;
    margin: 0 -5px;
    overflow: hidden;
    flex-wrap: wrap;
}
#thumbnailsShowMore{
    width: 20%;
    padding: 5px;
    display: none;
    flex-shrink: 0;
    cursor: pointer;
}
.p-thumbnail:nth-child(1n+18) ~ #thumbnailsShowMore{
    display: flex;
}
.thumbnailsShowMore-inner{
    background: $gray-300;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 100%;
    padding: 10px;
    font-size: 14px;
}
@media screen and (max-width: 991px){
    .p-thumbnails-wrapper{
        margin-top: 5px;
    }
}

.p-thumbnail{
    display: flex;
    padding: 5px;
    width: 20%;
    flex-shrink: 0;
}
@media screen and (min-width: 992px){
    .p-thumbnail:nth-child(1n+18){
        display: none;
    }
}
@media screen and (max-width: 991px){
    .p-thumbnail:nth-child(1n+8){
        display: none;
    }
}
.p-thumbnail[href*="infografika"]{
    display: none;
}
.p-thumbnail img{
    min-width: 100%;
}
.thumbnail-prev, .thumbnail-next{
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translate(0,-50%);
    height: 36px;
    width: 36px;
    background-color: rgba($black, 0.6);
    cursor: pointer;
    display: none;
}
.thumbnail-next{
    left: auto;
    right: 10px;
}

.p-thumbnails-inner.--active{
    & > div > .p-thumbnail{
        display: flex !important;
    }
    #thumbnailsShowMore{
        display: none;
    }
}

@media screen and (max-width: 991px){
    .p-image-wrapper{
        width: 100%;
    }
}



.p-info-wrapper{
    display: flex;
    flex-direction: column;
    padding-left: 30px;
    width: 40%;
}
@media screen and (max-width: 991px){
    .p-info-wrapper{
        width: 100%;
    }
}

.p-info-wrapper > p{
    display: flex;
    order: 10;
    margin-bottom: 10px;
}
.p-info-wrapper > p > a{
}
.p-info-wrapper .detail-parameters{
    width: 100%;
}
.p-final-price-wrapper{
    order: 25;
    display: flex;
    flex-direction: column;
    padding-top: 20px;
}
.p-final-price-wrapper .price-standard{
    text-decoration: line-through;
    color: $gray-700;
    font-weight: 500;
    font-size: 18px;
}
.p-final-price-wrapper .price-standard > span{
    padding-right: 10px;
}
.p-final-price-wrapper .price-standard > span.empty{
    display: none;
}
.p-final-price-wrapper .price-save{
    display: none;
}
.price-final{
    display: flex;
    align-items: baseline;
}
.price-additional{
    font-size: 14px;
    color: $gray-700;
}
.price-final-holder{
    color: $red-700;
    font-size: 24px;
    font-weight: 700;
}
.price-final-currency{
    font-size: 18px;
}
.price-final-dph{

}
.price-final .price-final-pre{
    font-size: 18px;
}
.price-measure{

}
.availability-value{
    font-size: 14px;
    font-weight: 600;
    color: $green-900;
    order: 24;
    padding-top: 10px;
    display: flex;
}
.availability-label{
    display: flex;
    
    &::before{
        content: "";
        display: flex;
        height: 22px;
        width: 22px;
        margin-right: 10px;
        background-image: url("../icons/delivery-green.svg");
        background-size: contain;
        background-repeat: no-repeat;
    }
}
.default-variant .availability-label::before{
    content: none;
    color: $green-900;
}
.availability-amount{

}
.detail-parameters{
    display: flex;
    order: 20;
    width: 50%;
}
.detail-parameters tbody{
    width: 100%;
}
.variant-list{
    display: flex;
    flex-direction: column;
    padding: 10px 0;
}
.variant-list th{
    padding-bottom: 5px;
    display: flex;
    width: 100%;
}
.variant-list select{
    width: 360px;
    max-width: 100%;
    background: $white;
    height: 48px;
    padding: 0 15px;
    border: 1px solid $gray-400;
    -webkit-appearance: none;
    background-image: url("../icons/arrow-down.svg");
    background-position: right 10px center;
    background-repeat: no-repeat;
    background-size: 14px 14px;
    border-radius: 0;
}
.variant-list select::-ms-expand{
    display: none;
}
.variant-list select:invalid:focus{
    outline: 1px solid $red-700;
  }


@media screen and (max-width: 767px){
    .variant-list select{
        width: 100%;
    }
    .detail-parameters{
        width: 100%;
    }
}
@media screen and (max-width: 500px){

}


#simple-variants{
    margin: 0 -5px;
}
.advanced-parameter{
    position: relative;
    padding: 5px;
    margin: 0;

    input{
        -webkit-appearance: none;
        opacity: 0;
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
    }
    &:hover{
        .advanced-parameter-tooltip{
            display: flex;

            &::after{
                content: "";
                display: flex;
                height: 8px;
                width: 8px;
                position: absolute;
                left: 50%;
                bottom: 0;
                transform: translate(-50%, 50%) rotate(45deg);
                background: $black;
            }
        }
    }
}
.advanced-parameter-tooltip{
    display: none;
    position: absolute;
    font-size: 12px;
    white-space: nowrap;
    background: $black;
    color: $white;
    left: 50%;
    top: 0;
    transform: translate(-50%, -24px);
    padding: 2px 5px;
    z-index: 1;

    &::after{
        display: none;
    }
}
.variant-default{
    display: none;
}
.advanced-parameter .parameter-value{
    display: none;
}
.advanced-parameter-inner{
    display: flex;
    height: 34px;
    width: 34px;
    border-radius: 50%;
    position: relative;
    border: 2px solid $gray-400;
    cursor: pointer;
}
.advanced-parameter-inner.yes-before{
    border-color: $green-700;
}
.advanced-parameter-inner.yes-before::after{
    content: "";
    background-image: url("../icons/check.svg");
    background-position: center center;
    background-size: 16px 16px;
    background-repeat: no-repeat;
    height: 16px;
    width: 16px;
    display: flex;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.add-to-cart{
    display: flex;
    order: 40;
    padding-top: 20px;
}
.add-to-cart .quantity{
    margin-right: 10px;
    position: relative;
    max-width: 140px;
}
.add-to-cart .amount{
    height: 60px;
    line-height: 60px;
    padding: 0 10px;
    width: 100%;
    border: 0;
    background: $gray-100;
    text-align: center;
}
.add-to-cart .increase, .add-to-cart .decrease{
    position: absolute;
    right: 0px;
    top: 0px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    cursor: pointer;

    &::before, &::after{
        content: "";
        background: $gray-800;
        height: 2px;
        width: 10px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    &::after{
        height: 10px;
        width: 2px;
    }
    &:hover, &:active, &:focus{
        background: $gray-300;
    }
}
.add-to-cart .decrease{
    left: 1px;
    right: auto;
    &::after{
        content: none;
    }
}
.add-to-cart .add-to-cart-button{
    padding: 0 60px;
    height: 60px;
}
.p-short-description{
    order: 5;
    font-size: 14px;
    padding-top: 0px;
    color: $gray-700;
}
.p-short-description  p{
    margin-bottom: 10px;
}
.social-buttons-wrapper{
    display: none;
}

.p-gifts-wrapper{
    order: 50;
    padding-top: 20px;
}
.p-gifts-heading{
    font-weight: 600;
    padding: 10px 0;
}
.p-gift{
    display: flex;
    align-items: center;
    border: 1px solid $gray-400;
    padding: 10px;
}
.p-gift-image{
    height: 60px;
    width: 60px;
}
.p-gift-name{
    padding-left: 10px;
}
.p-gift-name a{
    font-size: 14px;
    color: $gray-800;

    &:hover, &:active, &:focus{
        text-decoration: none;
    }
}
.p-gift-name a span{
    font-size: 14px;
    color: $red-700;
    display: flex;
}

.p-detail-tabs-wrapper .responsive-nav{
    display: flex;
    margin-top: 0px;
}
.p-detail-tabs-wrapper .responsive-nav .navigation-btn{
    display: none;
}
    

@media screen and (max-width: 991px){
    .p-image-wrapper {
        padding-right: 10px;
    }
    .p-info-wrapper{
        padding-left: 10px;
    }
    .p-detail-inner-header h1 {
        font-size: 24px;
    }
    .p-short-description{
        display: none;
    }
    .p-detail-tabs-wrapper .responsive-nav{
        display: none;
    }
}


@media screen and (min-width: 992px){
    .p-detail-tabs-wrapper .responsive-nav{
        position: sticky;
        top: 0;
        z-index: 2;
    }
}


.p-detail-tabs-wrapper{

    .shp-tabs-holder{
        width: 100%;
        background: $gray-300;
        margin-bottom: 20px;
    }
    .p-detail-tabs li{
        position: relative;
        &::after{
            content: "";
            width: 1px;
            height: 20px;
            background: #e1e1e1;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            position: absolute;
        }

        &.active{
       
            
            a{
             
            }
        }
    }  
    .p-detail-tabs a{
        padding: 12px 20px;  
        display: flex;
        color: $gray-900;
        text-transform: uppercase;
        font-weight: 600;
        font-size: 15px;

        &:hover, &:active, &:focus{
            text-decoration: none;
            color: $gray-800;
            background: $gray-400;
        }
    }
    .p-detail-tabs a[href="#ratingTab"]{
        background-image: url("../icons/star-full.svg");
        background-repeat: no-repeat;
        background-position: right 8px top 8px;
        background-size: 12px;
    }
    .navigation-btn{
        display: none !important;
    }
    ul{
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        flex-wrap: wrap;
    }
    li{

    }
    a{
  
    }

    .detail-parameters{
        order: 0;
        font-size: 14px;
        display: table;
        
        tr{
            height: 30px;
            padding: 5px;
        }
        tr:nth-child(odd){
            background: $gray-300;
        }
        th{
            color: $gray-700;
            padding-right: 10px;
            padding: 5px;
            font-weight: 400;
        }
        td{
            color: $gray-800;
            padding: 5px;
        }
    }
}

.p-detail-tabs li:nth-child(2){
    order: 0;
}

.p-detail-tabs li:nth-last-child(2){
    order: 20;
}
.p-detail-tabs li:last-child{
    order: 10;
}



#cboxContent{
    text-align: center;
}
#cboxPrevious, #cboxNext{
    font-size: 0;
    display: flex;
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
    background: $white;
    border-radius: 50%;
    height: 48px;
    width: 48px;
    border: 0;
    -webkit-appearance: none;
    background-image: url($i-arrow-left);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 14px 14px;
    cursor: pointer;
}
#cboxNext{
    left: auto;
    right: 10px;
    transform: translateY(-50%) rotate(180deg);
}
#cboxCurrent{
    display: none;
    opacity: 0;
}


.description-inner{
    padding-top: 20px;
    display: flex;
    flex-direction: column;
}
.basic-description{
    display: flex;
    flex-direction: column;
}

.basic-description > h3, .basic-description > .rte > h2{
    display: none;
    position: relative;
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 20px;
    margin-top: 0px;
    padding-bottom: 15px;
    color: $gray-800;
    width: 100%;

    &::after{
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        height: 6px;
        width: 40px;
        background: $green-200;
    }
}
.basic-description > .rte > h2{
    display: flex;
    strong{
        font-weight: 600;
    }
    *{
        color: $gray-800 !important;
        font-size: 24px;
    }
    br{
        display: none;
    }
}
.extended-description{
    display: flex;
    flex-wrap: wrap;
    order: 0;
    margin-bottom: 20px;
}
.extended-description > h3{
    text-align: center;
    position: relative;
    display: flex;
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 20px;

    padding-bottom: 15px;
    color: $gray-800;
    width: 100%;

    &::after{
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        height: 6px;
        width: 40px;
        background: $green-200;
    }
}


#jsUnavailableCombinationMessage{
    margin: 0;
    padding: 10px;
    background: $pink-200;
    text-align: center;
    color: $pink-800;
    font-size: 14px;
    a{
        color: $gray-800;
    }
}

#relatedFiles, .shp-tab-link[href="#relatedFiles"]{
    display: none !important;
} 
#relatedFiles{
    flex-wrap: wrap;
}
#relatedFiles li{
    padding: 20px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 20%;
}


